"use client"

import * as React from "react"
import {
  CartesianGrid,
  Line,
  LineChart,
  ResponsiveContainer,
  XAxis,
  YAxis,
} from "recharts"

import { Button } from "@/components/ui/button"
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card"
import {
  ChartContainer,
  ChartLegend,
  ChartLegendContent,
  ChartTooltip,
  ChartTooltipContent,
} from "@/components/ui/chart"
import { Skeleton } from "@/components/ui/skeleton"
import { useHistoryMetrics } from "../hooks"

const DATE_FORMATTER = new Intl.DateTimeFormat("ko-KR", { month: "numeric", day: "numeric" })
const FULL_DATE_FORMATTER = new Intl.DateTimeFormat("ko-KR", {
  year: "numeric",
  month: "2-digit",
  day: "2-digit",
})
const TIME_FORMATTER = new Intl.DateTimeFormat("ko-KR", {
  hour: "2-digit",
  minute: "2-digit",
})

function formatShortDate(dateString) {
  const date = new Date(`${dateString}T00:00:00Z`)
  if (!Number.isFinite(date.getTime())) return dateString
  return DATE_FORMATTER.format(date)
}

function formatFullDateTime(date) {
  if (!(date instanceof Date) || Number.isNaN(date.getTime())) return null
  return `${FULL_DATE_FORMATTER.format(date)} ${TIME_FORMATTER.format(date)}`
}

function buildSeriesData(categories) {
  const dateSet = new Set()
  const categoryMaps = new Map()

  for (const category of categories) {
    const map = new Map()
    for (const point of category.values) {
      dateSet.add(point.date)
      map.set(point.date, point.total)
    }
    categoryMaps.set(category.key, map)
  }

  const sortedDates = Array.from(dateSet).sort((a, b) => {
    const aTime = Date.parse(`${a}T00:00:00Z`)
    const bTime = Date.parse(`${b}T00:00:00Z`)
    return aTime - bTime
  })

  return sortedDates.map((date) => {
    const entry = { date, label: formatShortDate(date) }
    for (const category of categories) {
      const map = categoryMaps.get(category.key)
      entry[category.key] = map?.get(date) ?? 0
    }
    return entry
  })
}

function buildChartConfig(categories) {
  const config = {}
  categories.forEach((category, index) => {
    config[category.key] = {
      label: category.label,
      color: `hsl(var(--chart-${((index % 5) + 1).toString()}))`,
    }
  })
  return config
}

function RangeControl({ label, value, onChange, name }) {
  return (
    <label className="flex flex-col gap-1 text-xs font-medium text-muted-foreground">
      <span>{label}</span>
      <input
        name={name}
        type="date"
        value={value ?? ""}
        onChange={(event) => onChange(event.target.value || null)}
        className="h-9 rounded-md border border-input bg-background px-3 text-sm text-foreground shadow-sm focus-visible:outline-none focus-visible:ring-2 focus-visible:ring-ring focus-visible:ring-offset-2"
      />
    </label>
  )
}

function LoadingState() {
  return (
    <div className="grid gap-6 lg:grid-cols-2">
      {Array.from({ length: 6 }).map((_, index) => (
        <Card key={index}>
          <CardHeader className="pb-4">
            <Skeleton className="h-4 w-32" />
            <Skeleton className="h-3 w-44" />
          </CardHeader>
          <CardContent>
            <Skeleton className="h-[240px] w-full" />
          </CardContent>
        </Card>
      ))}
    </div>
  )
}

function DailyTotalsChart({ data }) {
  const chartData = React.useMemo(
    () =>
      data.map((item) => ({
        ...item,
        label: formatShortDate(item.date),
      })),
    [data]
  )

  const config = React.useMemo(
    () => ({
      total: { label: "ESOP 진행" },
      sendJira: { label: "JIRA 발송" },
    }),
    []
  )

  return (
    <Card className="flex h-full flex-col">
      <CardHeader className="gap-2">
        <CardTitle>일별 진행 현황</CardTitle>
        <CardDescription>전체 진행 건수와 JIRA 발송 건수를 비교합니다.</CardDescription>
      </CardHeader>
      <CardContent className="flex-1">
        <ChartContainer config={config} className="h-[280px]">
          <ResponsiveContainer width="100%" height="100%">
            <LineChart data={chartData} margin={{ left: 8, right: 16, top: 8, bottom: 8 }}>
              <CartesianGrid strokeDasharray="4 4" className="stroke-border/60" />
              <XAxis
                dataKey="label"
                tickLine={false}
                axisLine={false}
                tickMargin={8}
                className="text-xs text-muted-foreground"
              />
              <YAxis
                allowDecimals={false}
                width={48}
                axisLine={false}
                tickLine={false}
                className="text-xs text-muted-foreground"
              />
              <ChartTooltip content={<ChartTooltipContent />} />
              <ChartLegend content={<ChartLegendContent />} />
              <Line type="monotone" dataKey="total" name="진행" stroke="var(--color-total)" strokeWidth={2} dot={false} />
              <Line
                type="monotone"
                dataKey="sendJira"
                name="JIRA 발송"
                stroke="var(--color-sendJira)"
                strokeWidth={2}
                dot={false}
              />
            </LineChart>
          </ResponsiveContainer>
        </ChartContainer>
      </CardContent>
    </Card>
  )
}

function CategoryTrendChart({ title, description, categories }) {
  const normalizedCategories = React.useMemo(
    () => (Array.isArray(categories) ? categories : []),
    [categories]
  )

  const data = React.useMemo(() => buildSeriesData(normalizedCategories), [normalizedCategories])
  const config = React.useMemo(() => buildChartConfig(normalizedCategories), [normalizedCategories])

  if (normalizedCategories.length === 0) {
    return null
  }

  return (
    <Card className="flex h-full flex-col">
      <CardHeader className="gap-2">
        <CardTitle>{title}</CardTitle>
        <CardDescription>{description}</CardDescription>
      </CardHeader>
      <CardContent className="flex-1">
        <ChartContainer config={config} className="h-[280px]">
          <ResponsiveContainer width="100%" height="100%">
            <LineChart data={data} margin={{ left: 8, right: 16, top: 8, bottom: 8 }}>
              <CartesianGrid strokeDasharray="4 4" className="stroke-border/60" />
              <XAxis
                dataKey="label"
                tickLine={false}
                axisLine={false}
                tickMargin={8}
                className="text-xs text-muted-foreground"
              />
              <YAxis
                allowDecimals={false}
                width={48}
                axisLine={false}
                tickLine={false}
                className="text-xs text-muted-foreground"
              />
              <ChartTooltip content={<ChartTooltipContent />} />
              <ChartLegend content={<ChartLegendContent />} />
              {normalizedCategories.map((category) => (
                <Line
                  key={category.key}
                  type="monotone"
                  dataKey={category.key}
                  name={category.label}
                  stroke={`var(--color-${category.key})`}
                  strokeWidth={2}
                  dot={false}
                />
              ))}
            </LineChart>
          </ResponsiveContainer>
        </ChartContainer>
      </CardContent>
    </Card>
  )
}

const DIMENSION_LABELS = {
  sdwt_prod: "SDWT 별",
  user_sdwt_prod: "User SDWT 별",
  eqp_id: "설비 별",
  main_step: "Main Step 별",
  sample_type: "Sample Type 별",
  line_id: "라인 별",
}

const DIMENSION_DESCRIPTIONS = {
  sdwt_prod: "상위 SDWT 기준으로 진행 추이를 확인합니다.",
  user_sdwt_prod: "사용자 SDWT별 히스토리를 보여줍니다.",
  eqp_id: "설비별 진행량 변화를 비교합니다.",
  main_step: "메인 공정 단계별 진행량입니다.",
  sample_type: "샘플 타입별로 일일 추이를 제공합니다.",
  line_id: "라인 ID별 진행량을 비교합니다.",
}

export function HistoryDashboardPage({ lineId }) {
  const { range, metrics, status, lastUpdated, refresh, defaultRange } = useHistoryMetrics({ lineId })
  const [pendingRange, setPendingRange] = React.useState(range)

  React.useEffect(() => {
    setPendingRange(range)
  }, [range])

  const handleApply = React.useCallback(() => {
    refresh(pendingRange)
  }, [pendingRange, refresh])

  const handleReset = React.useCallback(() => {
    setPendingRange(defaultRange)
    refresh(defaultRange)
  }, [defaultRange, refresh])

  const charts = React.useMemo(() => {
    const entries = Object.entries(metrics.breakdowns ?? {})
    return entries.map(([key, value]) => ({
      key,
      label: DIMENSION_LABELS[key] ?? value.label ?? key,
      description: DIMENSION_DESCRIPTIONS[key] ?? "",
      categories: value.categories ?? [],
    }))
  }, [metrics.breakdowns])

  const lastUpdatedLabel = React.useMemo(() => formatFullDateTime(lastUpdated), [lastUpdated])

  return (
    <div className="flex h-full flex-col gap-6 overflow-y-auto p-2">
      <section className="flex flex-wrap items-end gap-4 rounded-lg border border-border bg-background/60 p-4">
        <div className="flex flex-col gap-2">
          <span className="text-sm font-semibold text-foreground">기간 선택</span>
          <div className="flex flex-wrap items-center gap-3">
            <RangeControl
              label="시작일"
              name="from"
              value={pendingRange?.from}
              onChange={(value) => setPendingRange((prev) => ({ ...(prev ?? {}), from: value }))}
            />
            <RangeControl
              label="종료일"
              name="to"
              value={pendingRange?.to}
              onChange={(value) => setPendingRange((prev) => ({ ...(prev ?? {}), to: value }))}
            />
            <div className="flex items-center gap-2">
              <Button onClick={handleApply} disabled={status.isLoading}>
                적용
              </Button>
              <Button variant="outline" onClick={handleReset} disabled={status.isLoading}>
                초기화
              </Button>
            </div>
          </div>
        </div>
        <div className="ml-auto flex flex-col items-end text-xs text-muted-foreground">
          <span>
            범위: {range?.from ?? "-"} ~ {range?.to ?? "-"}
          </span>
          {lastUpdatedLabel ? <span>업데이트: {lastUpdatedLabel}</span> : <span>데이터를 불러오는 중…</span>}
          {status.error ? <span className="text-destructive">{status.error}</span> : null}
        </div>
      </section>

      {status.isLoading && metrics.dailyTotals.length === 0 ? (
        <LoadingState />
      ) : (
        <div className="grid gap-6 lg:grid-cols-2">
          <DailyTotalsChart data={metrics.dailyTotals} />
          {charts.map((chart) => (
            <CategoryTrendChart
              key={chart.key}
              title={chart.label}
              description={chart.description}
              categories={chart.categories}
            />
          ))}
        </div>
      )}
    </div>
  )
}
