// src/features/line-dashboard/hooks/useHistoryMetrics.js
"use client"

import * as React from "react"

const DAY_IN_MS = 86_400_000
const DEFAULT_RANGE_DAYS = 30

function toDateString(date) {
  return date.toISOString().slice(0, 10)
}

function createDefaultRange() {
  const today = new Date()
  const to = toDateString(today)
  const fromDate = new Date(today.getTime() - (DEFAULT_RANGE_DAYS - 1) * DAY_IN_MS)
  return { from: toDateString(fromDate), to }
}

function toNumber(value) {
  if (typeof value === "number") return Number.isFinite(value) ? value : 0
  if (typeof value === "bigint") return Number(value)
  const parsed = Number(value)
  return Number.isFinite(parsed) ? parsed : 0
}

function normalizeSeriesPoint(point) {
  if (!point || typeof point !== "object") return null
  const date = typeof point.date === "string" ? point.date : null
  if (!date) return null
  return {
    date,
    total: toNumber(point.total),
    sendJira: toNumber(point.sendJira ?? point.send_jira),
  }
}

function normalizeCategory(category, fallbackKey) {
  if (!category || typeof category !== "object") return null
  const key = typeof category.key === "string" ? category.key : fallbackKey
  const label = typeof category.label === "string" ? category.label : key
  const values = Array.isArray(category.values)
    ? category.values
        .map((point) => normalizeSeriesPoint(point))
        .filter((point) => point !== null)
    : []

  return {
    key,
    label,
    total: toNumber(category.total),
    sendJira: toNumber(category.sendJira ?? category.send_jira),
    values,
  }
}

function normalizeBreakdowns(breakdowns) {
  if (!breakdowns || typeof breakdowns !== "object") return {}

  const normalized = {}
  for (const [key, value] of Object.entries(breakdowns)) {
    if (!value || typeof value !== "object") continue

    const categories = Array.isArray(value.categories)
      ? value.categories
          .map((category, index) => normalizeCategory(category, `${key}-${index}`))
          .filter((category) => category !== null)
      : []

    normalized[key] = {
      key: typeof value.key === "string" ? value.key : key,
      label: typeof value.label === "string" ? value.label : key,
      column: typeof value.column === "string" ? value.column : null,
      categories,
    }
  }

  return normalized
}

function normalizeHistoryPayload(payload) {
  if (!payload || typeof payload !== "object") {
    return {
      table: null,
      range: createDefaultRange(),
      lineId: null,
      dailyTotals: [],
      breakdowns: {},
    }
  }

  const dailyTotals = Array.isArray(payload.dailyTotals)
    ? payload.dailyTotals
        .map((item) => normalizeSeriesPoint(item))
        .filter((item) => item !== null)
    : []

  const range =
    payload.range && typeof payload.range === "object"
      ? {
          from: typeof payload.range.from === "string" ? payload.range.from : null,
          to: typeof payload.range.to === "string" ? payload.range.to : null,
        }
      : createDefaultRange()

  return {
    table: typeof payload.table === "string" ? payload.table : null,
    range,
    lineId: typeof payload.lineId === "string" ? payload.lineId : null,
    dailyTotals,
    breakdowns: normalizeBreakdowns(payload.breakdowns),
  }
}

export function useHistoryMetrics({ lineId, initialFrom, initialTo } = {}) {
  const defaultRangeRef = React.useRef(createDefaultRange())

  const baseRange = React.useMemo(() => {
    const fallback = defaultRangeRef.current
    return {
      from: initialFrom ?? fallback.from,
      to: initialTo ?? fallback.to,
    }
  }, [initialFrom, initialTo])

  const [range, setRange] = React.useState(baseRange)
  const [metrics, setMetrics] = React.useState(() => ({
    table: null,
    range: baseRange,
    lineId: null,
    dailyTotals: [],
    breakdowns: {},
  }))
  const [status, setStatus] = React.useState({ isLoading: false, error: null })
  const [lastUpdated, setLastUpdated] = React.useState(null)

  const fetchMetrics = React.useCallback(
    async (overrideRange) => {
      const targetRange = overrideRange ?? range ?? defaultRangeRef.current

      if (!lineId) {
        setMetrics((prev) => ({ ...prev, range: targetRange }))
        return { ok: false, error: "Missing line id" }
      }

      setStatus({ isLoading: true, error: null })

      try {
        const params = new URLSearchParams()
        params.set("lineId", lineId)
        if (targetRange?.from) params.set("from", targetRange.from)
        if (targetRange?.to) params.set("to", targetRange.to)

        const response = await fetch(`/api/line-dashboard/history?${params.toString()}`, {
          cache: "no-store",
        })

        let payload = null
        try {
          payload = await response.json()
        } catch (error) {
          payload = null
        }

        if (!response.ok) {
          const message =
            payload && typeof payload === "object" && typeof payload.error === "string"
              ? payload.error
              : `Failed to load history metrics (${response.status})`
          throw new Error(message)
        }

        const normalized = normalizeHistoryPayload(payload)

        setMetrics(normalized)
        setRange(normalized.range ?? targetRange)
        setStatus({ isLoading: false, error: null })
        setLastUpdated(new Date())

        return { ok: true }
      } catch (error) {
        const message = error instanceof Error ? error.message : "Failed to load history metrics"
        setStatus({ isLoading: false, error: message })
        return { ok: false, error: message }
      }
    },
    [lineId, range]
  )

  React.useEffect(() => {
    setRange(baseRange)
    setMetrics((prev) => ({ ...prev, range: baseRange }))
    setStatus({ isLoading: false, error: null })
    setLastUpdated(null)

    if (!lineId) return

    fetchMetrics(baseRange)
  }, [lineId, baseRange, fetchMetrics])

  return React.useMemo(
    () => ({
      range,
      metrics,
      status,
      lastUpdated,
      refresh: fetchMetrics,
      setRange,
      defaultRange: defaultRangeRef.current,
    }),
    [range, metrics, status, lastUpdated, fetchMetrics]
  )
}
