// src/components/ui/chart.jsx
"use client"

import * as React from "react"
import { Legend, Tooltip as RechartsTooltip } from "recharts"

import { cn } from "@/lib/utils"

const DEFAULT_COLORS = [
  "hsl(var(--chart-1))",
  "hsl(var(--chart-2))",
  "hsl(var(--chart-3))",
  "hsl(var(--chart-4))",
  "hsl(var(--chart-5))",
]

export function ChartContainer({ config, className, children, style, ...props }) {
  const colorVars = React.useMemo(() => {
    const entries = Object.entries(config ?? {})
    if (entries.length === 0) return undefined

    const styles = {}
    for (const [index, [key, value]] of entries.entries()) {
      const targetColor = value?.color ?? DEFAULT_COLORS[index % DEFAULT_COLORS.length]
      styles[`--color-${key}`] = targetColor
    }
    return styles
  }, [config])

  return (
    <div
      className={cn("relative h-[320px] w-full", className)}
      style={{ ...colorVars, ...style }}
      {...props}
    >
      {children}
    </div>
  )
}

export function ChartTooltip({ content, className, ...props }) {
  const renderContent = React.useMemo(() => {
    if (!content) return undefined

    return (tooltipProps) => {
      if (React.isValidElement(content)) {
        return React.cloneElement(content, tooltipProps)
      }

      if (typeof content === "function") {
        return content(tooltipProps)
      }

      return null
    }
  }, [content])

  return (
    <RechartsTooltip
      {...props}
      content={renderContent}
      wrapperClassName={cn(
        "pointer-events-none rounded-lg border border-border bg-background/90 p-3 text-xs shadow-lg backdrop-blur",
        className
      )}
    />
  )
}

export function ChartTooltipContent({ active, payload, label, indicator = "dot", className }) {
  if (!active || !payload || payload.length === 0) return null

  return (
    <div className={cn("grid gap-2", className)}>
      {label ? <div className="text-xs font-medium text-muted-foreground">{label}</div> : null}
      <div className="grid gap-1">
        {payload.map((item) => {
          const color = item.color ?? `var(--color-${item.dataKey})`
          const name = item.name ?? item.dataKey
          return (
            <div key={item.dataKey ?? name} className="flex items-center justify-between gap-4">
              <div className="flex items-center gap-2">
                <span
                  className={cn(
                    "h-2 w-2 rounded-full",
                    indicator === "line" ? "h-0.5 w-4 rounded-none" : "h-2 w-2 rounded-full"
                  )}
                  style={{ backgroundColor: color }}
                />
                <span className="text-sm font-medium text-foreground">{name}</span>
              </div>
              <span className="text-sm font-semibold tabular-nums text-foreground">{item.value}</span>
            </div>
          )
        })}
      </div>
    </div>
  )
}

export function ChartLegend({ content, ...props }) {
  const renderContent = React.useMemo(() => {
    if (!content) return undefined

    return (legendProps) => {
      if (React.isValidElement(content)) {
        return React.cloneElement(content, legendProps)
      }

      if (typeof content === "function") {
        return content(legendProps)
      }

      return null
    }
  }, [content])

  return (
    <Legend
      {...props}
      content={renderContent}
      wrapperStyle={{ color: "hsl(var(--muted-foreground))" }}
    />
  )
}

export function ChartLegendContent({ payload, className }) {
  if (!payload || payload.length === 0) return null

  return (
    <div className={cn("flex flex-wrap items-center gap-3 text-xs text-muted-foreground", className)}>
      {payload.map((entry) => {
        const color = entry.color ?? `var(--color-${entry.dataKey})`
        return (
          <div key={entry.dataKey ?? entry.value} className="flex items-center gap-2">
            <span className="h-2 w-2 rounded-full" style={{ backgroundColor: color }} />
            <span className="font-medium text-foreground">{entry.value}</span>
          </div>
        )
      })}
    </div>
  )
}
